<template>
    <div id="bg" class="bg">
        <div class="title">网络事件涉案标准评估中立平台</div>
        <div class="mid-nav">
            <ul>
                <li><i class="el-icon-document"></i> 评估规则</li>
                <li><i class="el-icon-printer"></i> 评估费用</li>
                <li><i class="el-icon-search"></i> 评估特点与优势</li>
            </ul>
        </div>
        <div class="point">评估服务指引 <i class="el-icon-bottom"></i></div>
        <div class="place"></div>
    </div>
</template>

<script>

export default {
    data(){
        return {
        }
    },
    methods:{
    },
    mounted(){

    }
}
</script>

<style>
.bg{
    z-index: 2;
    width: 100vw;
    height: 200vh;
    position: relative;
    text-align: center;
    overflow: auto;
}

.title{
    position: absolute;
    top: 20vh;
    left: 50%;
    transform: translateX(-50%);
    color: #fff;
    font-family: 'ceym';
    font-size: 68px;
    width: 1000px;
    font-weight:bold;
    z-index: 200;
}
.title:after{
    position: absolute;
    right: 20px;
    bottom: -60px;
    content:"前沿性 中立性 专业性 扩展性";
    font-size:24px;
    font-family:'ceym';
    z-index:2;
}
.point{
    position: absolute;
    top: 90vh;
    color: #fff;
    font-family: 'ceym';
    font-size: 32px;
    font-weight:bold;
    left: 50%;
    transform: translateX(-50%);
    animation: lingLight 2s ease-in-out infinite;
    cursor: pointer;
}
.place{
    height: 100vh;
    width: 300px;
}
@keyframes lingLight {
  0% {
    opacity: 1;
  }
   50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.mid-nav{
    position: absolute;
    top: 65vh;
    width: 100vw;
    height: 100px;
}
.mid-nav ul{
	list-style: none;
	width: 1200px;
	margin: auto;
    overflow: hidden;
    background-color: #ffffff40;
    box-sizing: border-box;
    height: 58px;
    border: 1px solid #ffffff40;
    word-break:keep-all
}
.mid-nav ul li:nth-child(2) {
    border-left: #fff 1px solid;
    border-right: #fff 1px solid;
}
.mid-nav li{
	float: left;
	width: 398px;
    transition:1s;
    height: 60px;
    line-height: 60px;
    font-size: 24px;
    color: #fff;
    font-family: 'ceym';
    box-sizing: border-box;
    cursor: pointer;
}
.mid-nav img{
		vertical-align: top;
}

.mid-nav ul:hover li{
		width: 248px;
}
	/*这个跟上面的位置不能调转，因为优先级一样。*/
.mid-nav ul li:hover{
		width: 698px;
}

  .bg::-webkit-scrollbar {
  /*滚动条整体样式*/
  width : 8px;  /*高宽分别对应横竖滚动条的尺寸*/
  }
  .bg::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  box-shadow   : inset 0 0 5px rgba(0, 0, 0, 0.2);
  background   : #fff;
  }
  .bg::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow   : inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background   :  linear-gradient(to bottom, #19778c, #095f88);
  }
</style>


